/* #6079AB */

/* notification animation - swipe in from right */
@keyframes swipe-in {
    0%   {left:160%;}
    100%  {left:0%;}
}

/* notification animation - swipe out to right */
@keyframes swipe-out {
    0%   {left:0%;}
    100%  {left:160%;}
}

/* sets no margin to body / makes all text centered / extra large / bold */
body {
    margin: 0;
    text-align: center;
    font-size: x-large;
    font-weight: bold;
}

/* sets buttons / text divs to have blue background / white text / rounded corners / solid black boarder */
.default, .Counter, .Totals, #Notification, #Exit {
    background-color: #6079AB;
    color: white;
    border-radius: 10px;
    height: 40px;
    font-weight: bold;
    border: 2px solid black;
}

/* sets height / width / position of exit fullscreen button */
#Exit {
    height: 50px;
    width: 100px;
    position: absolute;
    left: 50px;
    top: 3vh;
}

/* sets width / font size of elements with default class */
.default {
    width: 125px;
    font-size: medium;
}

/* sets width / font size of elements with counter class */
.Counter {
    width: 50px;
    font-size: 2e1;
}

/* sets width / font size / verticle alignment of elements with notification id or totals class */
#Notification, .Totals {
    width: 200px;
    font-size: large;
    text-align: center;
    line-height: 40px;
}

/* sets animation setings to class notif-in */
.notif-in {
    animation-name: swipe-in;
    animation-duration: 2.5s;
    animation-timing-function: ease;
    position: relative;
}

/* sets animation setings to class notif-out */
.notif-out {
    animation-name: swipe-out;
    animation-duration: 2.5s;
    animation-timing-function: ease;
    position: relative;
}

/* sets height / width / position / how borders act of product info table */
#Prod_Info_Table {
    position: absolute;
    height: 60vh;
    width: 25vw;
    left: -25vw;
    border-collapse: collapse;
}

/* sets height of rows in product info tables */
#Prod_Info_Table > tr {
    height: 10%;
}

/* sets solid black border to product info table's tds */
#Prod_Info_Table > tr > td {
    border: 2px solid black;
}

/* sets width / how borders act of basket table */
#Basket_Table {
    position: absolute;
    width: 80vw;
    border-collapse: collapse;
}

/* sets height of element with id Headers - basket table row 1 */
#Headers {
    height: 10vh;
}

/* sets solid black boarder to basket table / basket tables rows / table headers */
#Basket_Table, #Basket_Table > tr, #Headers > th {
    border: 2px solid #000000;
}

/* sets height of product images */
.Prod_IMG_Container, #Prod_IMG_Div, #Prod_IMG {
    height: 15vh;
}

/* sets width / how boarders will act / background as white for element with id PopUp_Table - confirm delete table */
#PopUp_Table {
    background-color: #ededed;
    width: 30vw;
    border-collapse: collapse;
}

/* sets solid black border to tds of confirm delete table */
#PopUp_Table > tr > td {
    border: 3px solid #000000;
}

/* sets margin / padding as nothing / position of basket overview divs / back to basket button / checkout button */
#Total_Price, #Total_Quant, #Diff_Quant, #Checkout_Button, #BFB {
    position: absolute;
    left: calc(90vw - 100px);
    padding: 0;
    margin: 0;
}

/* sets height / width of checkout / back from basket / fullscreen buttons */
#Checkout_Button, #BFB, #fullscreen {
    line-height: 0px;
    width: 204px;
    height: 44px;
}

/* sets blur to elements with class blur */
.blur {
    filter: blur(5px);
}

/* removes blur from elements with class NoBlur */
.NoBlur {
    filter: blur(0px);
}

/* sets position of element with id fullscreen_div - fullscreen buttons container */
#fullscreen_div {
    position: absolute;
    top: calc(50% - 25px);
    left: calc(50vw - 150px);
    z-index: 5;
}

#Basket_Button {
    position: absolute;
    left: calc(94vw - 62.5px);
    top: calc(91.75vh - 20px);
}

#BTS_Button {
    position: absolute;
    left: calc(94vw - 62.5px);
    top: calc(86.75vh - 20px);
}

#Prod_Info_Table_Div {
    position: absolute;
    left: 100vw;
}

#BFB {
    top: 90vh;
}

#Total_Price {
    top: 30vh;
}

#Total_Quant {
    top: 36vh;
}

#Diff_Quant {
    top: 42vh;
}

#Notif_container {
    left: 91.55vw;
    top: 10vh;
}

#Checkout_Button {
    top: 80vh;
}

